* Fanta.css (Fantasy + CSS)
* Version 0.1.0
* https://github.com/jamezmca/fantacss
*
* Sections
* 1. Content sectioning
* 2. Text content
* 3. Inline text semantics
* 4. Image and multimedia
* 5. Tables
* 6. Forms
* 7. Interactive elements
*
*/

