/* @media (prefers-color-scheme: dark) { */
:root {
  --background-primary: #030615;
  --background-secondary: rgb(23 37 84);
  --background-tertiary: #121424;
  --background-accent: "";
  --background-compliment: "";
  --background-gradient: "";
  --background-muted: #1a1e32;

  --color-primary: rgb(219 234 254);
  --color-secondary: "";
  --color-tertiary: "";
  --color-accent: "";
  --color-compliment: "";
  --color-gradient: "";
  --color-muted: ;
  --color-link: #60a5fa;
  --color-link-transparent: rgba(37, 99, 235, 0.2);

  --color-success: "";
  --color-warning: "";
  --color-error: "";
  --color-info: "";
  --color-highlight: #fef9c3;

  --gradient-start: #0084ff;
  --gradient-end: #00fffb;

  --border-primary: #29325b;
  --border-secondary: #93c5fd;
  --border-highlight: #4649af;
  --border-tertiary: "";

  --shadow-dark: "";
  --shadow-light: "";
  --shadow-text: "";

  --padding-small: 1rem;
  --padding-large: 2rem;

  --border-radius-small: 0rem;
  --border-radius-large: 0rem;
  --highlight-border-radius: 0rem;

  --text-selection: "";
}

/* } */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Noto Sans SC", sans-serif;
}

body {
  background: var(--background-primary);
  color: var(--color-primary);
  font-size: 0.875rem;
  line-height: 1.6rem;
}

/* Special */
.text-gradient {
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-image: linear-gradient(
    180deg,
    var(--gradient-start) 0,
    var(--gradient-end) 100%
  );
  background-size: 100%;
  -webkit-box-decoration-break: clone;
}

/* Typography */
h1,
h2,
h3,
h4,
h5,
h6,
button {
  font-family: "Orbitron", sans-serif; /* 标题 & 按钮 */
}

/* h1, */
.text-large {
  font-size: 1.875rem;
  line-height: 2.25rem;
}

h1 {
  font-size: 1.5rem;
  line-height: 2rem;
}

.special-shadow {
  text-shadow: -1px -1px 4px #0022ffbf, 2px 2px 10px #0022ffbf,
    0 0 20px #0022ffbf;
  color: white;
}

h2 {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

h3,
.text-medium {
  font-size: 1.125rem;
  line-height: 1.75rem;
}

h4 {
  font-size: 1rem;
  line-height: 1.5rem;
}

h5,
h6 {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

p {
  display: block;
  width: fit-content;
}

span {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
}

/* Content Sectioning */

main {
}

header {
}

footer {
}

nav {
}

section {
}

address {
  font-style: normal;
}

aside {
  float: right;
  width: 40%;
  padding: 0.75rem;
  margin: 0.5rem;
  font-style: italic;
  color: var(--color-primary);
  background-color: var(--background-muted);
  border-radius: var(--border-radius-large);
}

/* Text Content */
blockquote {
  position: relative;
  padding-left: 1.5rem;
  margin: 0;
}

blockquote::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  border-left: 7px solid var(--border-primary);
  border-radius: 6px;
}

blockquote footer {
  padding-top: 1rem;
}

dd {
  padding-bottom: 11px;
}

dl {
}

dt {
  font-weight: bold;
}

figure {
}

figcaption {
  padding-top: 10px;
  font-size: 0.8rem;
}

hr {
}

ul,
ol {
  list-style-position: inside;
  padding-left: 1rem;
}

li {
  line-height: 1.6em;
}

/* Inline Text Elements */

a {
  color: var(--color-link);
}

a:active,
a:focus,
a:hover {
  text-decoration: none;
}

.link-button {
  border: none;
  background: var(--background-primary);
  color: var(--color-primary);
  box-shadow: none;
  padding: 0.5rem 1rem;
}

.link-button:hover {
  box-shadow: none;
  transform: unset;
  text-decoration: underline;
}

mark,
samp,
kbd,
code,
time {
  border-radius: var(--highlight-border-radius, 4px);
  box-decoration-break: clone;
  -webkit-box-decoration-break: clone;
}

mark {
  background-color: var(--color-highlight);
  padding: 0 4px;
}

samp {
  font-weight: bold;
  padding: 0.5rem 1rem;
  background-color: var(--background-muted);
  color: var(--color-primary);
}

kbd,
time {
  padding: 0rem 0.5rem;
  background-color: var(--background-muted);
  color: var(--color-primary);
}

code,
pre {
  font-size: 0.9em;
  padding: 0.2rem 0.5rem;
  background: var(--background-muted);
  border: 1px solid var(--border-primary);
  max-width: fit-content;
  overflow-x: auto;
}

pre > code {
  padding: 10px;
  border: 0;
  display: block;
  overflow-x: auto;
}

pre {
  border-radius: var(--border-radius-large);
}

sup,
sub {
  line-height: normal;
}

/* Image and multimedia */
audio {
  width: 100%;
}

audio,
img,
video {
  border-radius: var(--border-radius-large);
  max-width: 100%;
}

img {
  height: auto;
}

/* Tables */
table {
  width: fit-content;
  border: 1px solid var(--border-primary);
  background: var(--background-muted);
  border-radius: var(--border-radius-small);
}

table tr:last-child td:first-child {
  border-bottom-left-radius: 8px;
}

table tr:last-child td:last-child {
  border-bottom-right-radius: 8px;
}

table tr:first-child th:first-child {
  border-top-left-radius: 8px;
}

table tr:first-child th:last-child {
  border-top-right-radius: 8px;
}

th {
  background-color: var(--background-muted);
}

td {
  background: var(--background-primary);
}

td,
th {
  text-align: left;
  padding: 8px;
}

thead {
  border-collapse: collapse;
}

tfoot {
  border-top: 1px solid black;
}

table tr:hover td,
tbody tr:nth-child(even):hover td {
  background-color: var(--background-muted);
}

/* Form elements */
input,
button,
select,
optgroup,
textarea {
  /* margin: 0; */
}

button,
select,
input[type="submit"],
input[type="button"],
input[type="checkbox"],
input[type="range"],
input[type="radio"] {
  cursor: pointer;
}

button {
  color: var(--color-primary);
  background-color: var(--background-secondary);
  width: fit-content;
  font-family: inherit;
  font-size: inherit;
  font-weight: 500;
  padding: 0.25rem 1.25rem;
  border: 1.5px solid var(--border-secondary);
  border-radius: var(--border-radius-small);
  box-shadow: 2px 2px 0 0 var(--border-secondary);
  transition-duration: 200ms;
}

button:hover {
  box-shadow: 0 0 0 0 var(--border-secondary);
  transform: translate(2px, 2px);
}

button[disabled]:hover {
  box-shadow: 2px 2px 0 0 var(--border-secondary);
  transform: translate(0, 0);
}

button:disabled,
button[disabled] {
  border: 1.5px solid var(--border-secondary);
  cursor: initial;
  opacity: 0.55;
}

label {
  display: block;
  max-width: fit-content;
  font-weight: 500;
}

input,
textarea,
select {
  font-size: 1em;
  background-color: var(--background-muted);
  border: 1px solid var(--border-secondary);
  color: var(--color-primary);
  padding: 0.5rem 0.75rem;
  border-radius: var(--border-radius-small);
  width: 100%;
  /* max-width: fit-content; */
  outline: none;
  appearance: none;
}

input:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

input[type="number"].buttonless {
  appearance: textfield;
  -moz-appearance: textfield;
}

input[type="number"].buttonless::-webkit-inner-spin-button {
  display: none;
  appearance: none;
}

input[type="number"].buttonless::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="checkbox"],
input[type="radio"] {
  padding: 0.5rem;
  width: fit-content;
  vertical-align: middle;
  position: relative;
  margin: 0.5rem 0.25rem 0.5rem 0.5rem;
  /* margin-right: 0.33em;
margin-top: 0.31em; */
}

input[type="checkbox"] {
  border-radius: 7px;
  margin-left: 0;
}

input[type="radio"] {
  border-radius: 100%;
}

input[type="checkbox"]:checked,
input[type="radio"]:checked {
  background: var(--border-secondary);
}

input[type="range"] {
  vertical-align: middle;
  padding: 0;
}

input[type="color"] {
  appearance: none;
  border: none;
  outline-style: none;
  padding: initial;
  max-width: initial;
  height: 2rem;
  width: 3rem;
}

select:is([multiple]) {
  background: none;
  height: fit-content;
}

fieldset:focus-within,
input:focus-within,
textarea:focus-within,
select:focus-within {
  border-color: var(--border-highlight);
}

fieldset:hover,
input:hover,
textarea:hover,
select:hover {
  border-color: var(--border-highlight);
}

progress {
  appearance: none;
  height: 1rem;
  margin: 0.75rem 0;
}

progress::-webkit-progress-bar {
  background: var(--background-main);
  border: 1px solid var(--border-primary);
  border-radius: var(--highlight-border-radius);
}

progress::-webkit-progress-value {
  background-color: var(--color-link);
  border-radius: var(--border-radius-small);
}

progress::-moz-progress-bar {
  background-color: var(--color-link);
  border-radius: var(--border-radius-small);
}

fieldset {
  border: 1px solid var(--border-primary);
  border-radius: var(--border-radius-small);
  margin: 0;
  margin-bottom: 6px;
  padding: 1rem;
  max-width: fit-content;
}

/* Interactive elements */
details {
  border: 1px solid var(--border-primary);
  border-radius: var(--border-radius-small);
  padding: 0.5rem 0.75rem;
}

summary {
  font-weight: bold;
}

details[open] summary {
  border-bottom: 1px solid var(--border-primary);
  margin-bottom: 0.5rem;
}

.card,
.card-button {
  background-color: var(--background-muted);
  color: var(--color-primary);
  padding: 1rem;
}

.card {
  border: 1px solid var(--color-link-transparent);
}

.card-button {
  border: 1px solid var(--border-secondary);
}

.card-button {
  box-shadow: none;
}

.card-button:hover {
  transform: translate(0);
  box-shadow: none;
  border-color: var(--border-highlight);
}

.card-button-primary,
.card-button-secondary {
  border: none !important;
  box-shadow: none !important;
}

.card-button-primary {
  color: var(--background-primary);
  background: var(--color-link);
}

.card-button-secondary {
  color: var(--color-link);
  background: var(--color-link-transparent);
}

.card-button-primary:hover,
.card-button-secondary:hover {
  transform: none;
  opacity: 0.6;
}

@media (min-width: 640px) {
  body {
    font-size: 1rem;
    line-height: 1.5rem;
  }

  /* h1, */
  .text-large {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }

  h1 {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  h2 {
    font-size: 1.5rem;
    line-height: 2rem;
  }

  h3,
  .text-medium {
    font-size: 1.25rem;
    line-height: 1.75rem;
  }

  h4 {
    font-size: 1.125rem;
    line-height: 1.75rem;
  }

  h5,
  h6 {
    font-size: 1rem;
    line-height: 1.5rem;
  }

  button {
    width: fit-content;
  }

  input {
    max-width: 600px;
  }
}
